.patternPageMain {
  display: flex;
  justify-content:space-around;
  flex-direction: row;
  align-items: center;
  z-index: 2;
  gap: 50px;
  width: 100%;
  min-height: 100%;
  background-image: linear-gradient(133deg, rgba(253, 253, 253, 0.377), rgba(14, 13, 13, 0.397));
}

.patternPageMain p {
font-size: 2rem;
}


.aH {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  min-width: 120px;
  min-height: 20px;  
  margin: 0;
  padding: 0;
}
.aH p {
  position: relative;
  height: 20px;
  color: white;
  font-size: 15px;
  margin: 0;
  padding: 0;
  top: -5px;
  text-align: center;
  width: 100px;
}
.aH a {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  border-radius: 5px;
  height: 30px;
  width: 30px;
  margin: 2px;
}
.aH strong {
  text-align: center;
  color: white;
}

.patternImg {
  max-width: 500px;
  max-height: 800px;
  
}
.patternImg img {
  border: 1px solid rgb(165, 32, 158);
  width: 100%;
  height: auto;
  -webkit-box-shadow: 6px 6px 6px 6px rgba(6, 6, 6, 0.2);
  -moz-box-shadow: 6px 6px 6px 6px rgba(6, 6, 6, 0.2);
  box-shadow: 6px 6px 6px 6px rgba(6, 6, 6, 0.2);
}
.patternText {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

select {
  width: 100px;
  margin-left: 5px;
}


/*patternsul*/

.selectlink {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  font: 30px 'Fira Sans', sans-serif;
  margin: 0;
}
fieldset {
 border-color: rgb(73, 72, 72);
 width: 200px;
}



.selectlink input {
  margin: 0.4rem;
  cursor: pointer;
}


#buttonPattern {
  width: 200px;
  height: 50px;
  border: 1px solid black;
  cursor: pointer;
  color: black;
  font-size: 30px;
  background-color: #0fc5bcb4;
}


@media all and (min-width: 320px) and (max-width: 600px) {
  .patternPageMain {
    display: flex;
    flex-direction: column;
    gap: 50px;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    background-image: linear-gradient(133deg, rgba(253, 253, 253, 0.377), rgba(14, 13, 13, 0.397));
  }
  .patternImg img {
    border: 1px solid rgb(165, 32, 158);
    width: 100%;
    height: auto;
    -webkit-box-shadow: 6px 6px 6px 6px rgba(6, 6, 6, 0.2);
    -moz-box-shadow: 6px 6px 6px 6px rgba(6, 6, 6, 0.2);
    box-shadow: 6px 6px 6px 6px rgba(6, 6, 6, 0.2);
  }
  .patternText {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}